@import url(https://fonts.googleapis.com/css?family=Saira:700);
@import url(https://fonts.googleapis.com/css?family=Courgette&display=swap);
*,*:before,*:after{box-sizing:border-box}body{margin:0;background:#77ACDB;height:100vh;font-family:'Saira', sans-serif;font-weight:700;color:white}.nav-position{margin-top:-.5em}img{width:100%;top:0%;transition:top 1s}.nav{background-color:#65369D}button{color:white !important}button[aria-selected="true"]{color:#77ACDB !important}.wrapper{display:grid;place-items:center;position:absolute;width:80%;z-index:10;top:8em;left:-2em}h1{background:linear-gradient(to bottom, #ebf1f6 0%, #abd3ee 50%, #859ee2 51%, #d5ebfb 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#ebf1f6', endColorstr='#d5ebfb',GradientType=0 );-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;font-size:2.25em;transition:font-size 1s;position:absolute;text-transform:uppercase}h1:after{background:none;content:attr(data-text);left:0;position:absolute;text-shadow:1px -1px 0 rgba(255,255,255,0.5),3px 1px 3px rgba(255,0,255,0.85),-3px -2px 3px rgba(0,0,255,0.85),1px -2px 0 rgba(255,255,255,0.8);z-index:-3}h1:before{content:' ';position:absolute;top:-.5em;left:1.6em;width:3em;height:4.7em;background:url("/static/media/Triangle 1.511206f7.svg") no-repeat;background-size:4.5em;width:100%;z-index:-4}h2{text-shadow:1px -1px 0 rgba(255,255,255,0.5),2px 1px 2px rgba(255,0,255,0.85),-3px -2px 3px rgba(0,0,255,0.85),1px -2px 0 rgba(255,255,255,0.8);color:#ff00ea;font-size:2em;transform:rotateZ(-15deg);position:absolute;font-family:'Courgette', cursive;transition:font-size 1s;margin-top:3.5em}@media only screen and (min-width: 768px){img{top:0%}h1{font-size:2em}h2{font-size:2em}}@media only screen and (min-width: 950px){body{background-position:50% 0%}h1{font-size:3em}.wrapper{left:-6em}}@media only screen and (min-width: 1100px){img{top:0%}h1{font-size:3em}}@media only screen and (min-width: 1500px){img{top:0%}h1{font-size:4em}}

